<template>
  <div id="personal-data">
    <head-top title="个人资料"></head-top>
    <div class="form">
      <div class="form-item" v-for="(item,i) in personDatas" :key="i">
        <div class="fL">{{item.info}}</div>
        <div class="fR">{{item.value}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      personDatas: []
    };
  },
  computed: {},
  watch: {},
  created() {
    if (this.$store.getters.student) {
      const student = this.$store.getters.student;
      this.personDatas = [
        { info: "学号", value: this.$store.getters.userName },
        { info: "姓名", value: student["StudentName"] },
        { info: "性别", value: student["StudentGender"] === 1 ? "男" : "女" },
        { info: "学院", value: "睿和科技" },
        { info: "专业", value: "" },
        { info: "班级", value: "" }
      ];
    } else {
      this.personDatas = [
        { info: "学号", value: "" },
        { info: "姓名", value: "" },
        { info: "性别", value: "男" },
        { info: "学院", value: "" },
        { info: "专业", value: "" },
        { info: "班级", value: "" }
      ];
    }
    this.getPersonInfo();
  },
  methods: {
    getPersonInfo() {
      this.$mainHttp.student
        .getData({ studentID: this.$store.getters.studentID })
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              let student = res.Datas[0].Key;
              this.personDatas = [];
              this.personDatas = [
                { info: "学号", value: this.$store.getters.userName },
                { info: "姓名", value: student["StudentName"] },
                {
                  info: "性别",
                  value: student["StudentGender"] === 1 ? "男" : "女"
                },
                { info: "学院", value: res.Datas[0]["SchoolName"] },
                { info: "专业", value: res.Datas[0]["DisciplineName"] },
                { info: "班级", value: res.Datas[0]["ClassName"] }
              ];
            }
          }
        })
        .catch(err => {});
    },
    goBack() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang='less'>
@import url("./../../styles/common/base.less");
#personal-data {
  .form {
    width: 100%;
    padding-bottom: 2vh;
    background: rgba(255, 255, 255, 1);
  }
  .fL {
    font-size: @font-info;
    color: #333;
  }

  .fR {
    font-size: @font-info;
    color: #999;
  }
  .form-item {
    height: 8vh;
    width: 92vw;
    margin: 0 auto;
    line-height: 8vh;
    border-bottom: 1px solid rgba(221, 221, 221, 1);
  }
}
</style>


